import api from '../../utils/apiCaller'
import localStorage from '../../utils/localStorage'

// import router from '../../router'
const state = {
    // time
    part_exerciseDelay       : false,   //  
    part_exercise            : false,   //  
    part_exerciseTime        : '00:00', // total  ( )
    part_exerciseMode        : 0,       // 0:   , 1: time, 2: motion
    part_exerciseTimeModeTime: 25,      // time  
    part_puase               : false,   //  
    part_puaseTime           : '00:00', //  
    part_restTime            : 0,       //  
    part_restSet             : 0,       //   
    part_totalExerciseTime   : 0,       // total 
    part_totalRestTime       : 0,       // total 
    // exercise
    part_totalSet            : 0,       //   set
    part_playSet             : 0,       //   set
    part_weight              : 0,       //  
    part_goalCount           : 0,       //  count
    part_playItems: {
        setStartTime         : 0,       //   
        exerciseTotalCount   : 0,       //   count
        exerciseCount        : 0,       //   count
        detailItems          : []       //  
    },
}

const mutations = {
    MU_PART_PLAY_EXERCISE_DELAY (state, exerciseDelay) {
        state.part_exerciseDelay = exerciseDelay
    },
    MU_PART_PLAY_EXERCISE (state, exercise) {
        state.part_exercise = exercise
    },
    MU_PART_PLAY_EXERCISE_TIME (state, exerciseTime) {
        state.part_exerciseTime = exerciseTime
    },
    MU_PART_PLAY_EXERCISE_MODE (state, exerciseMode) {
        state.part_exerciseMode = exerciseMode
    },
    MU_PART_PLAY_EXERCISE_TIME_MODE_TIME (state, exerciseTimeModeTime) {
        state.part_exerciseTimeModeTime = exerciseTimeModeTime
    },
    MU_PART_PLAY_PAUSE (state, puase) {
        state.part_puase = puase
    },
    MU_PART_PLAY_PAUSE_TIME (state, puaseTime) {
        state.part_puaseTime = puaseTime
    },
    MU_PART_PLAY_REST_TIME (state, restTime) {
        state.part_restTime = restTime
    },
    MU_PART_PLAY_REST_SET (state, restSet) {
        state.part_restSet = restSet
    },
    MU_PART_PLAY_TOTAL_EXERCISE_TIME (state, totalExerciseTime) {
        state.part_totalExerciseTime = totalExerciseTime
    },
    MU_PART_PLAY_TOTAL_REST_TIME (state, totalRestTime) {
        state.part_totalRestTime = totalRestTime
    },
    MU_PART_PLAY_TOTAL_SET (state, totalSet) {
        state.part_totalSet = totalSet
    },
    MU_PART_PLAY_PLAY_SET (state, playSet) {
        state.part_playSet = playSet
    },
    MU_PART_PLAY_WEIGHT (state, weight) {
        state.part_weight = weight
    },
    MU_PART_PLAY_GOAL_COUNT (state, goalCount) {
        state.part_goalCount = goalCount
    },
    MU_PART_PLAY_PLAY_ITEMS (state, playItems) {
        state.part_playItems = playItems
    },
    MU_PART_PLAY_PLAY_ITEMS_SET_START_TIME (state, setStartTime) {
        state.part_playItems.setStartTime = setStartTime
    },
    MU_PART_PLAY_PLAY_ITEMS_EXERCISE_TOTAL_COUNT (state, exerciseTotalCount) {
        state.part_playItems.exerciseTotalCount = exerciseTotalCount
    },
    MU_PART_PLAY_PLAY_ITEMS_EXERCISE_COUNT (state, exerciseCount) {
        state.part_playItems.exerciseCount = exerciseCount
    },
    MU_PART_PLAY_PLAY_ITEMS_DETAIL_ITMES (state, detailItems) {
        state.part_playItems.detailItems.push(detailItems)
    },
}

const actions = {
    // Mirror  
    async ACT_PLAY_API_SET_SMART_MIRROR (){
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey  : memAuthKey
        }
        await api.play.API_SET_SMART_MIRROR(params)
    },
    //   set   upload
    async ACT_PART_PLAY_API_SET_PART_RESULT (Null, setData){
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey  : memAuthKey,               
            exKey       : 0,                            //  
            setStartTime: Number(setData.setStartTime), // set 
            setSeq      : Number(setData.setSeq),       // set 
            repetitions : Number(setData.repetitions),  //  
            exTime      : Number(setData.exTime),       //   ()
            restTime    : Number(setData.restTime),     //   ()
            exNo        : null,                         //   ( )
            setDetail   : setData.setDetail,            //   
        }
        // console.log('PART_RESULT',params)
        await api.play.API_SET_PART_RESULT(params)
    },
    //    
    async ACT_PART_PLAY_API_SET_PART_COMPLETE (Null, payload){
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey  : memAuthKey,
            exKey       : 0,                            //  
            exSortCode  : Number(payload.exSortCode),   //  
            isPresEx    : Number(payload.isPresEx),     //  
            exNo        : null,                         //   ( )
            isChoice    : 0,                            //  
            moveRange   : [],
            // {setSeq:1, moveTime:'02:22:02.136', moveRange: 825.0}
        }
        // console.log('PART_COMPLETE',params)
        await api.play.API_SET_PART_COMPLETE(params)
    },
}

const getters = {
    GET_PART_PLAY_EXERCISE_DELAY (state) {
        return state.part_exerciseDelay
    },
    GET_PART_PLAY_EXERCISE (state) {
        return state.part_exercise
    },
    GET_PART_PLAY_EXERCISE_TIME (state) {
        return state.part_exerciseTime
    },
    GET_PART_PLAY_EXERCISE_MODE (state) {
        return state.part_exerciseMode
    },
    GET_PART_PLAY_EXERCISE_TIME_MODE_TIME (state) {
        return state.part_exerciseTimeModeTime
    },
    GET_PART_PLAY_PAUSE (state) {
        return state.part_puase
    },
    GET_PART_PLAY_PAUSE_TIME (state) {
        return state.part_puaseTime
    },
    GET_PART_PLAY_REST_TIME (state) {
        return state.part_restTime
    },
    GET_PART_PLAY_REST_SET (state) {
        return state.part_restSet
    },
    GET_PART_PLAY_TOTAL_EXERCISE_TIME (state) {
        return state.part_totalExerciseTime
    },
    GET_PART_PLAY_TOTAL_REST_TIME (state) {
        return state.part_totalRestTime
    },
    GET_PART_PLAY_TOTAL_SET (state) {
        return state.part_totalSet
    },
    GET_PART_PLAY_PLAY_SET (state) {
        return state.part_playSet
    },
    GET_PART_PLAY_WEIGHT (state) {
        return state.part_weight
    },
    GET_PART_PLAY_GOAL_COUNT (state) {
        return state.part_goalCount
    },
    GET_PART_PLAY_PLAY_ITEMS (state) {
        return state.part_playItems
    },
}

export default {
    state,
    mutations,
    actions,
    getters
}
