const state = {
    report_exercise_time    : 0,    //   
    report_resultDetail     : [],   //   
    report_playItems: {
        totalSet            : 0,    //   set
        setStartTime        : 0,    //  
        exerciseSet         : 0,    //  set
        exerciseTotalCount  : 0,    //   count
        exerciseCount       : 0,    //  count
        detailItems         : []    //  
    },
}

const mutations = {
    MU_REPORT_EXERCISE_TIME (state, exercise_time) {
        state.report_exercise_time = exercise_time
    },
    MU_REPORT_RESULT_DETAIL (state, resultDetail) {
        state.report_resultDetail.push(resultDetail)
    },
    MU_REPORT_RESULT_DETAIL_RESET (state) {
        state.report_resultDetail = []
    },
    MU_REPORT_ITEMS (state, playItems) {
        state.report_playItems = playItems
    },
}

const actions = {
}

const getters = {
    GET_REPORT_EXERCISE_TIME (state) {
        return state.report_exercise_time
    },
    GET_REPORT_RESULT_DETAIL (state) {
        return state.report_resultDetail
    },
    GET_REPORT_ITEMS (state) {
        return state.report_playItems
    },
}

export default {
    state,
    mutations,
    actions,
    getters
}
