    program_exerciseTime        : '00:00', // total  ( )
    program_startSet            : 0,       //  
    program_startTime           : 0,       //   
    program_exerciseMode        : 0,       // 1: time, 2: motion
    program_puase               : false,   //  
    program_puaseTime           : '00:00', //  
    program_restTime            : 0,       //  
    program_restSet             : 0,       //   
    program_totalExerciseTime   : 0,       // total 
    program_totalRestTime       : 0,       // total 
    // exercise
